<template>
  <div class="paging"
    v-if="total > pageSize">
    <div :class="align">
      <el-pagination @current-change="currentChange"
        @size-change="sizeChange"
        :background="background"
        :page-size="pageSize"
        :page-sizes="pageSizes"
        :total="total"
        :pager-count="pagerCount"
        :current-page="currentPage"
        :layout="layout"
        :prev-text="prevText"
        :next-text="nextText"
        :disabled="disabled">
      </el-pagination>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    pageSize: {
      //
      type: Number,
      default: 0
    },
    pageSizes: {
      //
      // type: Number,
      default: () => [10, 20, 30]
    },
    total: {
      //
      type: Number,
      default: 0
    },
    pagerCount: {
      //
      type: Number,
      default: 7
    },
    currentPage: {
      //
      type: Number,
      default: 1
    },
    layout: {
      //
      type: String,
      default: 'total, sizes, prev, pager, next, jumper, ->'
    },
    prevText: {
      //
      type: String,
      default: ''
    },
    nextText: {
      //
      type: String,
      default: ''
    },
    disabled: {
      //
      type: Boolean,
      default: false
    },
    background: {
      //
      type: Boolean,
      default: true
    },
    align: {
      //leftright,center
      type: String,
      default: 'right'
    }
  },
  methods: {
    currentChange(index) {
      this.$emit('currentChange', index)
    },
    sizeChange(index) {
      this.$emit('sizeChange', index)
    }
  }
}
</script>

<style lang="stylus" scoped>
.left
  text-align: left
.right
  text-align: right
.center
  text-align: center
</style>



